<template>
	<div id="personalbanner">
		<div class="content_l">
			<p class="title">我的账号及资料</p>
			<div class="line"></div>
			<ul class="left_banner">
				<li :class="[currentIndex === index ? 'banner_active' : '']" v-for="(item, index) in bannerarray" @click="gochannel(index)">{{item}}</li>
			</ul>
		</div>
	</div>
</template>

<script>
export default {
	data () {
		return {
      bannerarray: ['账号信息', '订单信息', '常用信息']
		}
	},
	props: ['currentIndex'],
	methods: {
		gochannel(index) {
			switch (index) {
				case 0:
					this.currentIndex = index;
					window.location.href = 'personalaccount.html';
					break;
				case 1:
					this.currentIndex = index;
					window.location.href = 'orderinfo.html';
					break;
				case 2:
					this.currentIndex = index;
					window.location.href = 'commoninfo.html';
					break;
			}
		}
	}
}
</script>

<style lang="less">
	#personalbanner
		.content_l{
			width: 286px;
			float: left;
			display: inline-block;
			background-color: #fff;
			padding: 40px 38px 30px 38px;
			border:1px solid #e3e3e3;
			box-sizing: border-box;
			margin-right: 14px;
			border-radius: 4px;
			.title{
				font-size: 24px;
				letter-spacing: 2px;
				color:#414141;
			}
			.line{
				margin-top: 46px;
				height: 3px;
				width: 30px;
				background-color: #000;
			}
			.left_banner{
				margin-top: 35px;
				li{
					font-size: 20px;
					color:#c9c9c9;
					margin-bottom: 32px;
					cursor: pointer;
					&.banner_active{
						color:#1d1d1d !important;
					}
				}
			}
		}
</style>


