<template>
    <div id="personalaccount">
      <div class="mask" v-if="showmask"></div>
      <transition name="fade">
        <div class="loginbox_bot loginextra" v-if="showbox1">
        	<i class="el-icon-close" @click="closebox"></i>
          <input type="text" class="forget_pas" v-model="username" maxlength="11" v-on:keypress.enter="updateAccount">
          <div class="tips">修改昵称</div>
          <button class="confirmbutton" @click="updateAccount">确认</button>
        </div>
      </transition>
      <transition name="fade">
  	    <div class="loginbox_bot loginextra" v-if="status === 6">
  	      <input type="password" class="forget_pas" placeholder="请输入新的密码" v-model="forgetpass">
  	      <input type="password" class="forget_pas" placeholder="重复输入新的密码" v-model="forgetpass_again" v-on:keypress.enter="lastconfirm">
  	      <button class="confirmbutton confirmbutton1" @click="lastconfirm">确认</button>
  	    </div>
  	  </transition>
      <transition name="fade">
        <div class="loginbox_bot loginextra" v-if="status === 5">
        	<i class="el-icon-close" @click="closeit"></i>
          <p class="forget_title">输入验证码</p>
          <input type="button" class="forget_btn forget_btn1" :class="{ disabledcolor: isdiabled2 }" value="60秒后重试" ref="getagain" @click="getyzm">
          <div class="sixinput">
            <div class="line line1"></div>
            <div class="line line2"></div>
            <div class="line line3"></div>
            <div class="line line4"></div>
            <div class="line line5"></div>
            <input type="text" class="yzm_input" v-model="forget_yzm" ref="logininput">
            <transition name="fade">
             <div class="errortips" v-if="numerror">输入验证码错误,请重新输入</div>
            </transition>
          </div>
        </div>
      </transition>
      <transition name="fade">
        <div class="loginbox_bot loginextra" v-if="status === 1">
          <i class="el-icon-close" @click="closechange"></i>
          <div class="forget_num">
            <input type="text" class="forget_l" maxlength="11" placeholder="请输入手机号" v-model="forget_num" readonly="readonly">
            <input type="button" class="forget_r" value="获取验证码" @click="getyzm" ref="getbtn" :class="{ forget_disabled: isdiabled2 }">
          </div>
          <p class="tips tips1" v-if="default_mobile !== ''">通过绑定手机获取验证码修改密码,如手机号已不使用?<span class="changit" @click="hidebox">点击更改</span></p>
          <p class="tips tips1" v-if="default_mobile === ''">检测到您并未绑定手机号码,请先绑定手机号码<span class="changit" @click="hidebox">点击绑定</span></p>
        </div>
      </transition>
      <transition name="fade">
        <div class="loginbox_bot loginextra" v-if="status === 2">
          <i class="el-icon-close" @click="closechange"></i>
          <div class="forget_num">
            <input type="text" class="forget_l" maxlength="11" placeholder="请输入新的手机号" v-model="mobile_num">
            <input type="button" class="forget_r" value="获取验证码" @click="getyzm1" ref="getbtn2" :class="{ forget_disabled: isdiabled3 }">
          </div>
          <p class="tips tips1">输入新的手机号，通过获取验证码方式绑定新的手机号</p>
        </div>
      </transition>
      <transition name="fade">
        <div class="loginbox_bot loginextra" v-if="status === 3">
        	<i class="el-icon-close" @click="closeit"></i>
          <p class="forget_title">输入验证码</p>
          <input type="button" class="forget_btn forget_btn1" :class="{ disabledcolor: isdiabled3 }" value="60秒后重试" ref="getagain2" @click="getyzm1">
          <div class="sixinput">
            <div class="line line1"></div>
            <div class="line line2"></div>
            <div class="line line3"></div>
            <div class="line line4"></div>
            <div class="line line5"></div>
            <input type="text" class="yzm_input" v-model="mobile_yzm" ref="logininput2">
            <transition name="fade">
             <div class="errortips" v-if="numerror2">输入验证码错误,请重新输入</div>
            </transition>
          </div>
        </div>
      </transition>
  	  	<!-- 导航 -->
  		<cheeru-banner type="white" :index="5"></cheeru-banner>
  		<!-- <div class="mask"></div> -->
  		<div class="content">
  			<personalbanner :currentIndex="currentIndex"></personalbanner>
  			<div class="content_r">
  				<p class="title">我的账号</p>
  				<div class="line"></div>
  				<div class="account_item">
  					<div class="itemlogo"></div>
  					<div class="accountinfo">
  						<p>昵称</p>
  						<span v-if="default_name !== ''">{{default_name}}</span>
              <span v-else>暂无昵称</span>
              <button @click="showbox" v-if="default_name === ''">设置</button>
  						<button @click="showbox" v-else>更改</button>
  					</div>
  				</div>
  				<div class="account_item">
  					<div class="itemlogo"></div>
  					<div class="accountinfo">
  						<p>密码</p>
  						<span>**** ****</span>
  						<button @click="showchange">绑定 / 更改</button>
  					</div>
  				</div>
  				<div class="account_item">
  					<div class="itemlogo"></div>
  					<div class="accountinfo">
  						<p>手机号</p>
  						<span v-if="default_mobile !== ''">{{default_mobile}}</span>
              <span v-if="default_mobile === ''">未绑定</span>
  						<button @click="changemobile" v-if="default_mobile !== ''">更改</button>
              <button @click="changemobile" v-if="default_mobile === ''">绑定</button>
  					</div>
  				</div>
  			</div>
        <div class="clear"></div>
  		</div>
    </div>
</template>

<script>
import banner from '../../components/common/banner'
import footer from '../../components/common/footer'
import personalbanner from '../../components/common/personalbanner'
import { updateAccountUserName, backUpdatePhone, accountSendCode, checkVerifyCode, updateAccountPassport, phoneSendCode, backSendCode, backcheckVerifyCode, backCheckCode, backUpdatePassword, getUserName } from '../../api/api'
export default {
	data () {
		return {
      temname: '',
      default_accid: '',
      default_name: '',
      default_mobile: '',
      default_token: '',
			loading: true,
			currentIndex: 0,
			showmask: false,
			showbox1: false,
			username: '',
			status: 0,
			forget_num: '',
			numerror: false,
      numerror2: false,
			forget_yzm: '',
			forgetpass: '',
			forgetpass_again: '',
			isdiabled2: false,
			isdiabled3: false,
			mobile_num: '',
			mobile_yzm: '',
			times: '',
			times2: '',
			tem: '',
			tem2: ''
		}
	},
	watch: {
    // 个人中心修改密码填写验证码
    forget_yzm() {
      if (this.forget_yzm.length === 6) {
        this.$refs.logininput.blur();
        this.$refs.logininput.disabled = true;
        let info = {
          acc_id: this.default_accid,
          code_number: this.forget_yzm,
          token: this.default_token
        };
        backCheckCode(info).then((res) => {
          if (res.data !== '验证成功') {
            this.numerror = true;
            setTimeout(() => {
              if (this.$refs.logininput !== undefined) {
                this.$refs.logininput.disabled = false;
                this.$refs.logininput.focus();
              }
              this.numerror = false;
              this.forget_yzm = '';
            }, 1800);
            } else {
              this.status = 6;
              this.times = 0;
              this.tem = true;
              this.$message({
              type: 'success',
              message: '验证成功'
            });
            }
        });
      }
    },
    // 个人中心修改手机号码填写验证码
    mobile_yzm() {
      if (this.mobile_yzm.length === 6) {
        this.$refs.logininput2.blur();
        this.$refs.logininput2.disabled = true;
        let info = {
          acc_id: this.default_accid,
          token: this.default_token,
          code_number: this.mobile_yzm
        };
        backCheckCode(info).then((res) => {
          if (res.data !== '验证成功') {
            this.numerror2 = true;
            setTimeout(() => {
              if (this.$refs.logininput2 !== undefined) {
                this.$refs.logininput2.disabled = false;
                this.$refs.logininput2.focus();
              }
              this.numerror2 = false;
              this.mobile_yzm = '';
            }, 1800);
          } else {
            let info = {
              acc_id: this.default_accid,
              token: this.default_token,
              new_call_phone: this.mobile_num
            };
            backUpdatePhone(info).then((res) => {
              if (res.data === '修改成功') {
                let tem = {
                  'www': this.encryption(this.default_accid),
                  'xxx': this.encryption(this.default_token),
                  'yyy': this.encryption(this.mobile_num),
                  'zzz': this.default_name
                };
                sessionStorage.setItem(this.encryption('info'), JSON.stringify(tem));
                document.cookie = 'call_phone=' + this.mobile_num + '; expires=Session; path=/';
                this.$message({
                  type: 'success',
                  message: '修改手机号码成功'
                });
                setInterval(() => {
                  window.location.href = 'personalaccount.html';
                }, 1500);
              }
            });
          }
        });
      }
    }
  },
  created() {
    if (this.getCookie('login_type') !== '') {
      let info = {
        account_id: this.getCookie('user_id'),
        token: this.getCookie('token')
      };
      getUserName(info).then((res) => {
        if (res.data.user_name === '') {
          this.temname = res.data.other_user_name;
          this.default_name = res.data.other_user_name;
          sessionStorage.setItem('name', this.default_name);
          this.username = this.default_name;
        } else {
          this.temname = res.data.user_name;
          this.default_name = res.data.user_name;
          sessionStorage.setItem('name', this.default_name);
          this.username = this.default_name;
        }
      });
      let tem = {
        'www': this.encryption(this.getCookie('user_id')),
        'xxx': this.encryption(this.getCookie('token')),
        'yyy': this.encryption(this.getCookie('call_phone'))
      };
      // if (!(this.getCookie('user_name'))) {
      //   tem.zzz = decodeURI(this.getCookie('other_user_name'));
      // } else if (this.getCookie('user_name') === '') {
      //   tem.zzz = decodeURI(this.getCookie('other_user_name'));
      // } else {
      //   tem.zzz = this.getCookie('user_name');
      // }
      sessionStorage.setItem(this.encryption('info'), JSON.stringify(tem));
      this.default_accid = this.decrypt(JSON.parse(sessionStorage.getItem(this.encryption('info'))).www);
      this.default_token = this.decrypt(JSON.parse(sessionStorage.getItem(this.encryption('info'))).xxx);
      this.default_mobile = this.decrypt(JSON.parse(sessionStorage.getItem(this.encryption('info'))).yyy);
    } else {
      let cookie = sessionStorage.getItem(this.encryption('info'));
      if (!cookie) {
        window.location.href = '../../../views/login.html';
        sessionStorage.removeItem(this.encryption('info'));
      } else {
        this.default_accid = this.decrypt(JSON.parse(sessionStorage.getItem(this.encryption('info'))).www);
        this.default_name = JSON.parse(sessionStorage.getItem(this.encryption('info'))).zzz;
        this.default_token = this.decrypt(JSON.parse(sessionStorage.getItem(this.encryption('info'))).xxx);
        this.default_mobile = this.decrypt(JSON.parse(sessionStorage.getItem(this.encryption('info'))).yyy);
        this.username = this.default_name;
      }
    }
  },
	methods: {
    updateAccount() {
      if (this.username === '') {
        this.$message('请输入昵称');
      } else {
        let info = {
          acc_id: this.default_accid,
          token: this.default_token,
          user_name: this.username
        };
        updateAccountUserName(info).then((res) => {
          if (res.data === '修改成功') {
            document.cookie = 'user_name=' + encodeURI(this.username) + '; expires=Session; path=/';
            document.cookie = 'other_user_name=' + encodeURI(this.username) + '; expires=Session; path=/';
            let tem = {
              'www': this.encryption(this.default_accid),
              'xxx': this.encryption(this.default_token),
              'yyy': this.encryption(this.default_mobile),
              'zzz': this.username
            };
            sessionStorage.setItem(this.encryption('info'), JSON.stringify(tem));
            this.$message({
              type: 'success',
              message: '操作成功'
            });
            setInterval(() => {
              window.location.href = 'personalaccount.html';
            }, 1500);
          } else {
            this.$message({
              type: 'error',
              message: '操作失败'
            });
          }
        });
      }
    },
    getCookie(name) {
      if (document.cookie.length > 0) {
        var start = document.cookie.indexOf(name + '=');
        if (start !== -1) {
        start = start + name.length + 1
        var end = document.cookie.indexOf(';', start);
        if (end === -1) end = document.cookie.length
        return unescape(document.cookie.substring(start, end))
        }
        }
        return ''
    },
		closeit() {
			this.showmask = false;
			this.status = 0;
			this.forget_yzm = '';
			this.mobile_yzm = '';
		},
		changemobile() {
      document.documentElement.scrollTop = document.body.scrollTop = 0;
      document.documentElement.style.overflowY = document.body.style.overflowY = 'hidden';
			this.showmask = true;
			this.status = 2;
		},
		hidebox() {
      document.documentElement.style.overflowY = document.body.style.overflowY = '';
			this.status = 2;
		},
		lastconfirm() {
      if (this.forgetpass === '') {
        this.$message('请输入密码');
      } else if (!/^(?![A-Z]+$)(?![a-z]+$)(?!\d+$)(?![\W_]+$)\S{8,16}$/.test(this.forgetpass)) {
        this.$message({
          type: 'error',
          message: '密码必须包含字母和数字,长度8位以上'
        });
      } else if (this.forgetpass_again === '') {
        this.$message('请再次输入密码');
      } else if (this.forgetpass !== this.forgetpass_again) {
        this.$message({
          type: 'error',
          message: '两次输入密码不一致'
        });
      } else {
        let info = {
          acc_id: this.default_accid,
          token: this.default_token,
          password: this.forgetpass,
          request_password: this.forgetpass_again
        };
        backUpdatePassword(info).then((res) => {
          if (res.data === '修改成功') {
            this.$message({
              type: 'success',
              message: '修改密码成功,请重新登录'
            });
            setInterval(() => {
              sessionStorage.removeItem('info');
              window.location.href = 'login.html';
            }, 1500);
          } else {
            this.$message({
              type: 'success',
              message: '修改密码失败,请重试'
            });
          }
        });
      }
    },
		showbox() {
      document.documentElement.scrollTop = document.body.scrollTop = 0;
      document.documentElement.style.overflowY = document.body.style.overflowY = 'hidden';
			this.showmask = true;
			this.showbox1 = true;
		},
		closebox() {
      document.documentElement.style.overflowY = document.body.style.overflowY = '';
			this.showmask = false;
			this.showbox1 = false;
		},
		showchange() {
      document.documentElement.scrollTop = document.body.scrollTop = 0;
      document.documentElement.style.overflowY = document.body.style.overflowY = 'hidden';
			this.status = 1;
			this.showmask = true;
      this.forget_num = this.default_mobile;
		},
		closechange() {
      document.documentElement.style.overflowY = document.body.style.overflowY = '';
			this.status = 0;
			this.showmask = false;
		},
		getagain() {
      this.$message({
        type: 'success',
        message: '获取验证码成功'
      });
      this.getcount();
    },
    getagain2() {
      this.$message({
        type: 'success',
        message: '获取验证码成功'
      });
      this.getcount2();
    },
		getcount() {
      if (this.$refs.getagain) {
        this.$refs.getagain.setAttribute('disabled', 'disabled');
        this.times = 60;
        this.isdiabled2 = true;
        this.tem = false;
        this.$refs.logininput.focus();
        var timer = setInterval(() => {
          this.isdiabled2 = true;
          this.times = this.times - 1;
          if (this.$refs.getagain === undefined) {
          } else {
            this.$refs.getagain.value = this.times + '秒后重试';
          }
          if (this.$refs.getbtn === undefined) {
          } else {
          	this.$refs.getbtn.setAttribute('disabled', 'disabled');
            this.$refs.getbtn.value = this.times + '秒后重试';
          }
          if (this.tem === true) {
          	clearInterval(timer);
          }
          if (this.times === 0) {
            clearInterval(timer);
            if (this.$refs.getbtn !== undefined) {
            	this.$refs.getbtn.removeAttribute('disabled');
            	this.$refs.getbtn.value = '获取验证码';
            }
            if (this.$refs.getagain !== undefined) {
            	this.$refs.getagain.value = '重新获取验证码';
            	this.$refs.getagain.removeAttribute('disabled');
            }
            this.isdiabled2 = false;
          }
        }, 1000);
      }
    },
    getcount2() {
      if (this.$refs.getagain2) {
        this.$refs.getagain2.setAttribute('disabled', 'disabled');
        this.times2 = 60;
        this.isdiabled3 = true;
        this.tem2 = false;
        this.$refs.logininput2.focus();
        var timer2 = setInterval(() => {
          this.isdiabled3 = true;
          this.times2 = this.times2 - 1;
          if (this.$refs.getagain2 === undefined) {
          } else {
            this.$refs.getagain2.value = this.times2 + '秒后重试';
          }
          if (this.$refs.getbtn2 === undefined) {
          } else {
          	this.$refs.getbtn2.setAttribute('disabled', 'disabled');
            this.$refs.getbtn2.value = this.times2 + '秒后重试';
          }
          if (this.tem2 === true) {
          	clearInterval(timer2);
          }
          if (this.times2 === 0) {
            clearInterval(timer2);
            if (this.$refs.getbtn2 !== undefined) {
            	this.$refs.getbtn2.removeAttribute('disabled');
            	this.$refs.getbtn2.value = '获取验证码';
            }
            if (this.$refs.getagain2 !== undefined) {
            	this.$refs.getagain2.value = '重新获取验证码';
            	this.$refs.getagain2.removeAttribute('disabled');
            }
            this.isdiabled3 = false;
          }
        }, 1000);
      }
    },
    // 个人中心修改密码第一步获取验证码
		getyzm() {
			if (this.forget_num === '') {
        this.$message('请输入手机号');
      } else if (!(/^1[34578]\d{9}$/.test(this.forget_num))) {
        this.$message('请输入正确的手机号');
      } else {
        let info = {
          acc_id: this.default_accid,
          call_phone: this.forget_num,
          token: this.default_token
        };
        backSendCode(info).then((res) => {
          if ((res.data === '短信已发送2，请注意查收') || (res.data === '短信已发送，请注意查收')) {
            this.$message({
              type: 'success',
              message: '获取验证码成功'
            });
            this.status = 5;
            this.isdiabled2 = true;
            this.$nextTick(() => {
              this.getcount();
            });
          } else {
            this.$message({
              type: 'error',
              message: res.data
            });
          }
        });
      }
    },
    // 个人中心修改手机号第一步获取验证码
    getyzm1() {
			if (this.mobile_num === '') {
        this.$message('请输入手机号');
      } else if (!(/^1[34578]\d{9}$/.test(this.mobile_num))) {
        this.$message('请输入正确的手机号');
      } else if (this.mobile_num === this.default_mobile) {
        this.$message({
          type: 'error',
          message: '手机号码没有改变,请输入新的手机号'
        });
      } else {
        let info = {
          acc_id: this.default_accid,
          token: this.default_token,
          call_phone: this.mobile_num
        };
        backSendCode(info).then((res) => {
          if ((res.data === '短信已发送2，请注意查收') || (res.data === '短信已发送，请注意查收')) {
            this.$message({
              type: 'success',
              message: '获取验证码成功'
            });
            this.status = 3;
            this.isdiabled3 = true;
            this.$nextTick(() => {
              this.getcount2();
            });
          }
        });
      }
    },
    setCookie(name, value, expiredays) {
      var exdate = new Date();
      exdate.setDate(exdate.getDate() + expiredays);
      document.cookie = name + '=' + escape(value) + ((expiredays === null) ? '' : '; expires=' + exdate.toGMTString());
    }
	},
	components: {
		'cheeru-banner': banner,
		'cheeru-footer': footer,
		'personalbanner': personalbanner
	}
}
</script>

<style lang="less">
input:-webkit-autofill {
	-webkit-box-shadow: 0 0 0px 1000px white inset;
}
::-webkit-input-placeholder { /* WebKit browsers */ 
	color: #999; 
} 
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
	color: #999; 
} 
::-moz-placeholder { /* Mozilla Firefox 19+ */ 
	color: #999; 
} 
:-ms-input-placeholder { /* Internet Explorer 10+ */ 
	color: #999; 
} 
.fade-enter-active {
    transition: all .5s ease;
}
.fade-leave-active {
    transition: all 0s ease;
}
.fade-enter, .fade-leave-active {
    transform: scale(0);
    opacity: 0;
}
	#personalaccount{
		padding-top: 140px;
		.mask{
      min-width: 1200px;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top:0;
      background-color: #000;
      opacity: .3;
      z-index: 1000;
    }
    .loginbox_bot{
    	height: 370px;
    	width: 502px;
    	position: absolute;
    	z-index: 1001;
    	top:50%;
    	left: 50%;
    	margin-left: -251px;
    	margin-top: -185px;
    	background-color: #fff;
    	padding:80px 50px 0 50px;
    	border:1px solid #e3e3e3;
    	box-sizing: border-box;
    	border-radius: 4px;
    	.forget_btn{
        position: absolute;
        right: 0;
        top:0;
        border:none;
        background-color: #fff;
        cursor: pointer;
        color:#1ee3b3;
        outline: none;
        font-size: 16px;
      }
      .forget_btn1{
        position: absolute;
        right:50px;
